#myTestimonial{
  margin: 30px 0px;
}
#myTestimonial .carousel-inner{
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
}
#myTestimonial  .carousel-inner img{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  border: 5px solid #85d6df;
  margin: 10px auto;
  box-shadow: 1px 2px 6.5px #000;
}
#myTestimonial .carousel-indicators{
  position: relative;
}
#myTestimonial .fa-angle-left:before,
#myTestimonial .fa-angle-right:before
{
  color:#000;
}
#myTestimonial .carousel-indicators li{
  background-color: #000;
}
#carouselExampleFade .carousel-control-next-icon,
#carouselExampleFade .carousel-control-prev-icon {
  width: 35px;
  height: 35px;
}

#carouselExampleFade .carousel-control-prev-icon {
   left: -35px;
  position: absolute;
  color: #000;
  width: 15px;
}

#carouselExampleFade .carousel-control-next-icon {
  right: -35px;
  position: absolute;
  color: #000;
  width: 15px;
}
#carouselExampleFade .carousel-inner img{
  height: auto;
}
#carouselExampleFade .pet-name{
  text-align: center;
  background-color: #fe908b;
  padding: 10px;
  font-size: 1.2rem;

}
.image-slider-box{
  background-color: grey;
  background-image: url(/static/media/services-bg.c7ec536f.jpg);
  background-position: center;
  background-attachment: fixed;
  color: #fff;
  background-size: cover;
}

.main-heading{
    position: relative;
  }
  .main-heading h1{
    margin-bottom: 2em;
    text-align: center;
    /* font-size: 2em; */
  }
  .main-heading .tile_underline {
    position: absolute;
    content: '';
    display: block;
    width: 4.998rem;
    height: 0.833rem;
    left: 0;
    right: 0;
    bottom: -20px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_1 {
    left: 0;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_1 {
    background-color: #fe908b;
  }
  .tile_underline span.tile_underline_semicircle {
    content: '';
    width: 1.666rem;
    height: 1.666rem;
    border-radius: 50%;
    position: absolute;
    bottom: 0;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_2 {
    background-color: #85d6df;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_2 {
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_3 {
    background-color: #ffd27a;
  }
  .tile_underline span.tile_underline_semicircle.semicircle_3 {
    right: 0;
  }
  .main-heading  h1:after {
    content: ' ';
    display: block;
    position: absolute;
    border-top: 1px solid #f0f0f0;
    height: 0;
    width: 100%;
    min-width: 2em;
    left: 0;
    bottom: -7px;
  }
  .main-heading .dark{
    color:#000;
  }
  .main-heading .light{
    color:#fff;
}
.main-heading .light:after{
    border-top: 1px solid #a2a2a2;
}
